;margin-top:1.7em;background-color:rgba(0,0,0,0.3);border-radius:0px 0px 20px 20px}#root #launch-manifest #next-launch span{display:inline-block;margin-bottom:0.3em;vertical-align:top}#root #launch-manifest #next-launch span .detail{display:inline-block;max-width:70%}#root #launch-manifest #next-launch b{display:inline-block;width:300px}#root #launch-manifest .header{font-size:3em;color:white;margin-bottom:1rem;width:80vw}#root #launch-manifest .launch-card{border-radius:7px;width:80vw;min-width:600px;min-height:500px;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;background-color:#ddd;margin-bottom:30px;padding:2em;display:flex}#root #launch-manifest .launch-card #pic{min-width:300px;height:auto}#root #launch-manifest .launch-card #info{padding-left:2em}#root #launch-manifest .launch-card #info span{margin-bottom:1.5em;display:inline-block;font-size:1.1em;line-height:1.4em}#root #launch-manifest .launch-card #info span b{display:block;font-size:1.15em}#launch-dot{z-index:150}.vehicle #rocketImg{transition:all 30s ease-in-out}.vehicle #rocketImg img{max-height:100%}.vehicle #rocketImg.launched{transform:translateY(-9999px)}@media only screen and (max-width: 1024px){#root #launch-manifest .launch-card{flex-direction:column}#root #launch-manifest .launch-card #pic{min-width:-webkit-fit-content;min-width:-moz-fit-content;min-width:fit-content;max-height:300px;height:300px;background-position:top !important;margin-bottom:1.5em}#root #launch-manifest .launch-card #info{padding:0}}@media only screen and (max-width: 767px){*{overflow-x:hidden;max-width:100vw}circle{visibility:hidden}#hud-container{overflow:hidden;background:linear-gradient(0, black, transparent)}#root #header{height:40px;font-size:1.2em;font-weight:bold}#root #main-content{top:40px;min-width:100%;max-height:50%}#root #main-content .vehicle div img{max-width:100%;max-height:100%}#root #launch-manifest{margin-top:calc(50% + 40px + 1em);padding-right:0;padding-left:0}#root #launch-manif